@import url(https://fonts.googleapis.com/css?family=Raleway|Rubik&display=swap);
/****************/
/* Landing Page */
/****************/

#title {
	margin-top: 120px;
}

#title h1 {
	font-size: 80px;
	margin: 15px;
}

#title h4 {
	color: #aaa;
	width: 80%;
	margin-left: auto;
	margin-right: auto;
}

#title ul.nav {
	margin: 50px auto 50px auto;
}

a {
	text-decoration: none;
	color: #98c9a3;
}
/************/
/* Defaults */
/************/

body {
  font-family: 'Raleway', sans-serif;
}


/*********/
/* Forms */
/*********/

input[type="email"],
input[type="number"],
input[type="search"],
input[type="text"],
input[type="tel"],
input[type="url"],
input[type="password"],
textarea {
  height: 38px;
  padding: 6px 10px; /* The 6px vertically centers text on FF, ignored by Webkit */
  background-color: #fff;
  border: 1px solid #D1D1D1;
  border-radius: 4px;
  box-shadow: none;
  box-sizing: border-box; 
/* Removes awkward default styles on some inputs for iOS */
  -webkit-appearance: none;
          appearance: none; 
}

textarea {
  min-height: 65px;
  padding-top: 6px;
  padding-bottom: 6px; 
}

label,
legend {
  display: block;
  margin-bottom: .5rem;
}

fieldset {
  padding: 0;
  border-width: 0; 
}

input[type="checkbox"],
input[type="radio"] {
  display: inline; 
}

input:focus, 
textarea:focus {
  outline: none !important;
  border-color: #98C9A3;
  box-shadow: none;
}



/***********/
/* Buttons */
/***********/

.button,
button,
input[type="submit"],
input[type="reset"],
input[type="button"] {
  display: inline-block;
  height: 38px;
  padding: 0 30px;
  color: #555;
  text-align: center;
  font-size: 11px;
  font-weight: 600;
  font-family: 'Raleway', sans-serif;
  line-height: 38px;
  letter-spacing: .1rem;
  text-transform: uppercase;
  text-decoration: none;
  white-space: nowrap;
  background-color: transparent;
  border-radius: 4px;
  border: 1px solid #bbb;
  cursor: pointer;
  box-sizing: border-box; 
}

.vertical-buttons {
  display: inline-block;
  margin-top: .5rem;
}

.button:hover,
button:hover,
input[type="submit"]:hover,
input[type="reset"]:hover,
input[type="button"]:hover,
.button:focus,
button:focus,
input[type="submit"]:focus,
input[type="reset"]:focus,
input[type="button"]:focus {
  color: #333;
  border-color: #888;
  outline: 0; 
}

.button.button-primary,
button.button-primary,
input[type="submit"].button-primary,
input[type="reset"].button-primary,
input[type="button"].button-primary {
  color: #FFF;
  background-color: #98C9A3;
  border-color: #98C9A3; 
}

.button.button-primary:hover,
button.button-primary:hover,
input[type="submit"].button-primary:hover,
input[type="reset"].button-primary:hover,
input[type="button"].button-primary:hover,
.button.button-primary:focus,
button.button-primary:focus,
input[type="submit"].button-primary:focus,
input[type="reset"].button-primary:focus,
input[type="button"].button-primary:focus {
  color: #FFF;
  background-color: #77BFA3;
  border-color: #77BFA3; 
}

/*********/
/* Lists */
/*********/

ul {
  list-style: circle inside;
}

ol {
  list-style: decimal inside;
}

ol, ul {
  padding-left: 0;
  margin-top: 0;
}

ul ul,
ul ol,
ol ol,
ol ul {
  margin: 1.5rem 0 1.5rem 3rem;
  font-size: 90%;
}

li {
  margin-bottom: 1rem;
}


/***********/
/* Columns */
/***********/
.column {
  position: relative;
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  box-sizing: border-box;
  float: left;
}

.left-column {
  width: 45%;
  margin-left: 4%;
  margin-right: 1%;
}

.right-column {
  width: 45%;
  margin-right: 4%;
  margin-left: 1%;
}

.column-container {
  width: 80%;
  margin: 0px auto 0px auto;
  z-index: 1;
}


/***********/
/* Banners */
/***********/
.banner {
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.banner-title {
  font-family: 'Rubik', sans-serif;
  color: #77BFA3;
  margin-left: 20px;
  margin-right: 20px;
}
/*******/
/* Nav */
/*******/

ul.nav {
    list-style-type: none;
    text-align: center;
    width: 80%;
}

ul.nav li {
    display: inline-block;
    padding: 0 15px;
}

ul.nav li a {
    display: block;
    position: relative;
    padding: 15px;
    text-decoration: none;
    color: #999;
}

li.link-underline a:after,
li.link-underline a:before {
    transition: all .5s;
}

li.link-underline a:hover {
    color: #98C9A3;
}

li.link-underline a:after {
    position: absolute;
    margin:auto;
    bottom: 0;
    left: 0;
    right: 0;
    width: 0;
    content: '.';
    color: transparent;
    background: #98C9A3;
    height: 1px;
}

li.link-underline a:hover:after {
    width: 100%;
}
/********************/
/* Question Display */
/********************/

 #question {
	margin-top: 100px;
	font-size: 45px;
}

/**********/
/* Footer */
/**********/
#footer {
	display: inline-block;
	color: #999;
	font-size: 10px;
	margin: 50px auto 30px auto;
	text-align: center;
	width: 100%;
	bottom: 0;
}

#footer a {
	text-decoration: none;
	color: #98C9A3;
}

#footer.warning {
	font-weight: 600;
	color: crimson;
}


/***********/
/* Results */
/***********/

#score-box {
	border: 1px solid #D1D1D1;
	border-radius: 4px;
	background-color: #77bfa3;
	display: inline-block;
	font-size: 40px;
	color: #fff;
	padding: 40px 30px 40px 30px;
	z-index: 2;
}

li.url {
	color: #999;
}

.correct-answer {
	font-weight: 800;
	color: #77bfa3;
}

.incorrect-answer {
	font-weight: 800;
	color: crimson;
}

#phishing-category {
	margin-left: 30px;
}

#phishing-category span {
	font-weight: bolder;
}

/**************/
/* Url Review */
/**************/

#url-review {
	display: block;
	margin-left: auto;
	margin-right: auto;
	z-index: 10;
	padding: 10px;
}

#url-review li {
	list-style-type: none;
}

.contracted {
	max-height: 0;
	overflow: hidden;
	transition: max-height 0.7s ease-out;
}

.expanded {
	max-height: 100vh;
	z-index: 11;
	overflow: hidden;
	transition: max-height 0.4s ease-in;
}

#url-structure {
	list-style-type: none;
	margin-top: 60px;
	margin-bottom: 10px;
	font-family: "Rubik", sans-serif;
	font-size: 22px;
}

#url-structure li {
	display: inline-block;
}

#example-urls {
	margin-bottom: 20px;
	color: #999;
}
/********************/
/* Topic Display */
/********************/

#topic-name {
    font-size: 45px;
}

